//  User profile page
#user-profile-pg {
    margin-top:142px;
    //  --------------------------------
    @media (max-width: $break_991) {
        margin-top:25px;
    }
    .grid-left {
        @media (max-width: $break_991) {
            display: none;
        }
        ul {
            max-width:265px;
            li {
                margin-bottom:20px;
                a {
                    display: block;
                    color:$grey2-clr;
                    background-color:$white-clr;
                    border-radius:12px;
                    padding:15px 24px;
                    font-size:16px;
                    line-height:20px;
                    font-weight: 600;
                    svg {
                        fill:$grey2-clr;
                        position: relative;
                        top: 3px;
                        margin-right: 20px;
                    }
                    &:hover, &.active {
                        color:$white-clr;
                        background-color:$primary-clr;
                        svg {
                            fill:$white-clr;
                        }
                    }
                }
            }
        }
    }
    /* ---------------------------- */
    .grid-right {
        .content {
            height:calc(100vh - 150px);
            padding-bottom:60px;
            overflow-y:auto;
            &::-webkit-scrollbar {
                display: none;
            }
            //  --- user info section
            .lavel-tropy-section {
                flex-wrap:wrap;
                background: $white-clr;
                border: 1px solid $grey-clr;
                border-radius: 20px;
                padding: 40px;
                margin-bottom:20px;
                @media (max-width: $break_767) {
                    padding: 25px 20px;
                    border-radius: 12px;
                }
                .left-sec {
                    display: flex;
                    flex-wrap:wrap;
                    .avatar-area {
                        max-width: 80px;
                        margin-right: 32px;
                        @media (max-width: $break_767) {
                            max-width: 60px;
                            margin: auto;
                            margin-bottom: 16px;
                        }
                    }
                    .details-area {
                        @media (max-width: $break_767) {
                            width:100%;
                        }
                        h1.title {
                            font-size: 36px;
                            line-height: 44px;
                            font-weight: 700;
                            letter-spacing: -0.02em;
                            margin-bottom: 15px;
                            @media (max-width: $break_767) {
                                font-size: 22px;
                                line-height: 28px;
                                letter-spacing: 0em;
                                text-align: center;
                            }
                        }
                        p {
                            font-size: 16px;
                            line-height: 22px;
                            &.lavel {
                                font-weight: 600;
                                margin-bottom: 2px;
                                @media (max-width: $break_767) {
                                    text-align: center;
                                }
                                span.highlight {
                                    color: $primary-clr;
                                }
                            }
                            &.amount-left {
                                font-weight: 400;
                                margin-bottom:0;
                                @media (max-width: $break_767) {
                                    text-align: center;
                                }
                                span.highlight {
                                    color: $grey2-clr;
                                }
                            }
                        }
                    }
                }
                .right-sec {
                    display: flex;
                    flex-wrap:wrap;
                    justify-content:space-between;
                    @media (max-width: $break_767) {
                        margin-top:15px;
                    }
                    .grids {
                        padding: 0;
                        text-align: center;
                        .lavel-image {
                            position: relative;
                            .progresss {
                                width: 90px;
                                height: 90px;
                                @media (max-width: $break_767) {
                                    width:70px;
                                    height:70px;
                                }
                                &.noselect {
                                    -webkit-touch-callout: none;
                                    -webkit-user-select: none;
                                    -khtml-user-select: none;
                                    -moz-user-select: none;
                                    -ms-user-select: none;
                                    user-select: none;
                                    cursor: default;
                                }
                                .track{
                                    stroke: $light-pink-clr;
                                }
                                .fill {
                                    stroke:$white-clr;
                                    stroke-dasharray: 219.99078369140625;
                                    stroke-dashoffset: -219.99078369140625;
                                    transition: stroke-dashoffset 1s;
                                }
                                .track, .fill {
                                    fill: rgba(0, 0, 0, 0);
                                    stroke-width: 3;
                                    transform: rotate(90deg)translate(0px, -80px);
                                }
                                &.blue {
                                    .fill {
                                        stroke: $primary-clr;
                                    }
                                }
                            }
                            img {
                                width:50px;
                                position: absolute;
                                left:50%;
                                top:50%;
                                transform:translateX(-50%) translateY(-55%);
                                @media (max-width: $break_767) {
                                    width:40px;
                                }
                            }
                        }
                        p {
                            font-size: 14px;
                            font-weight: 600;
                            line-height: 18px;
                            color: $grey2-clr;
                            margin-bottom:0;
                            @media (max-width: $break_767) {
                                font-size: 12px;
                                line-height:14px;
                            }
                        }
                        &.grids-dis {
                            opacity:0.25;
                            .lavel-image {
                                margin-bottom:18px;
                                .progresss {
                                    display: none;
                                }
                                img {
                                    width:80px;
                                    position: relative;
                                    top:5px;
                                    left:0;
                                    transform: translateY(0) translateX(0);
                                    @media (max-width: $break_767) {
                                        width:50px;
                                        top: 10px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            //  --- my statistics section
            .user-statistic {
                background: $white-clr;
                border: 1px solid $grey-clr;
                border-radius: 20px;
                padding: 40px;
                margin-bottom:20px;
                @media (max-width: $break_767) {
                    padding: 25px 20px;
                    border-radius: 12px;
                }
                .tab-header {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    flex-wrap:wrap;
                    margin-bottom:45px;
                    @media (max-width: $break_767) {
                        margin-bottom:25px;
                    }
                    h4 {
                        display: flex;
                        align-items:center;
                        font-size: 24px;
                        line-height: 30px;
                        font-weight: 700;
                        margin: 0;
                        // --------------------------------
                        @media (max-width: $break_991) {
                            font-size:18px;
                            line-height:24px;
                            margin-bottom:20px;
                            justify-content:space-between;
                            width: 100%;
                        }
                        .nav {
                            font-size:14px;
                            line-height:18px;
                            .nav-item {
                                margin-left:25px;
                                // --------------------------------
                                @media (max-width: $break_991) {
                                    margin-left:10px;
                                }
                                .nav-link {
                                    padding:0 2px;
                                    padding-bottom:6px;
                                    position: relative;
                                    font-weight:600;
                                    color:$grey2-clr;
                                    background-color:$no-clr;
                                    &:after {
                                        content:'';
                                        width:100%;
                                        height:2px;
                                        border-radius:5px;
                                        background-color:$white-clr;
                                        position: absolute;
                                        bottom: 0;
                                        left: 0;
                                    }
                                    &.active {
                                        color:$black-light;
                                        background-color:$no-clr;
                                        &:after {
                                            background-color:$primary-clr;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .btn-sec {
                        .btn {
                            font-size: 14px;
                            line-height: 18px;
                            padding: 10px 15px;
                            min-width: 90px;
                            &.dropdown-toggle {
                                // --------------------------------
                                @media (max-width: $break_767) {
                                    padding-right:50px;
                                }
                                &:after {
                                    content: "\f107";
                                    border: none;
                                    font-family: FontAwesome;
                                    font-size: 16px;
                                    right: 30px;
                                    top: 8px;
                                    position: absolute;
                                    vertical-align: unset;
                                    margin: 0;
                                }
                            }
                        }
                        .dropdown-menu {
                            font-size: 14px;
                            line-height:18px;
                            border-radius: 10px;
                            padding: 5px;
                            width: 100%;
                            background-color: $white-clr;
                            .dropdown-item {
                                text-align: center;
                                background-color:$white-clr;
                                border-radius:5px;
                                padding: 5px;
                                &:hover {
                                    background-color:$blue-bg-clr;
                                }
                            }
                        }
                    }
                }
                .tab-body {
                    .left_graph {
                        padding-left:0;
                        @media (max-width: $break_767) {
                            overflow-x: auto;
                        }
                        #bars {
                            width:100%;
                        }
                        .btn_sec {
                            display: flex;
                            margin-top:35px;
                            a {
                                display: inline-block;
                                padding:10px 24px;
                                font-size:14px;
                                line-height:18px;
                                margin-right:5px;
                                border:1px solid $grey-clr;
                                color:$grey2-clr;
                                border-radius:10px;
                                &:hover, &.active {
                                    color:$white-clr;
                                    background-color:$primary-clr;
                                    border-color:$primary-clr;
                                }
                                @media (max-width: $break_767) {
                                    padding:10px;
                                    font-size:12px;
                                    line-height:14px;
                                }
                            }
                        }
                    }
                    .right_graph {
                        padding-right:0;
                        border-left:1px solid $grey-clr;
                        @media (max-width: $break_767) {
                            border-top:1px solid $grey-clr;
                            border-left:none;
                            margin-top:40px;
                            padding-top:40px;
                        }
                        #donuts {
                            width:100%;
                        }
                    }
                }
            }
            //  --- my faqs section
            .user-faq {
                background: $white-clr;
                border: 1px solid $grey-clr;
                border-radius: 20px;
                padding: 40px;
                @media (max-width: $break_767) {
                    padding: 25px 20px;
                    border-radius: 12px;
                }
                .tab-header {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    flex-wrap:wrap;
                    margin-bottom:45px;
                    @media (max-width: $break_767) {
                        margin-bottom:25px;
                    }
                    h4 {
                        font-size: 24px;
                        line-height: 30px;
                        font-weight: 700;
                        margin: 0;
                        // --------------------------------
                        @media (max-width: $break_991) {
                            font-size:18px;
                            line-height:24px;
                            margin-bottom:20px;
                        }
                    }
                    .btn-sec {
                        a {
                            font-size:16px;
                            line-height:22px;
                            padding:10px 24px;
                            display: inline-block;
                            margin-left:5px;
                            color:$black-light;
                            border:1px solid $grey-clr;
                            border-radius:14px;
                            &:hover, &.active {
                                color:$white-clr;
                                background-color:$primary-clr;
                                border-color:$primary-clr;
                            }
                            @media (max-width: $break_767) {
                                padding:10px 15px;
                                font-size:12px;
                                line-height:16px;
                                border-radius:10px;
                            }
                        }
                    }
                }
                .tab-body {
                    .boxes {
                        margin-bottom:25px;
                        button {
                            width:100%;
                            border:none;
                            background-color:$no-clr;
                            font-size:16px;
                            line-height:22px;
                            font-weight:600;
                            color:$black-light;
                            text-align: left;
                            padding:0;
                            padding-right: 30px;
                            position: relative;
                            margin-bottom:15px;
                            &:hover, &:focus, &[aria-expanded="true"] {
                                color:$primary-clr;
                                outline:none;
                                box-shadow:none;
                            }
                            &.collapsed {
                                margin-bottom:0;
                            }
                            &:after {
                                content:'\f067';
                                position: absolute;
                                right:0;
                                top:0;
                                font-family: FontAwesome;
                                font-weight: normal;
                            }
                            &[aria-expanded="true"] {
                                &:after {
                                    content:'\f068';
                                }
                            }
                            @media (max-width: $break_767) {
                                font-size:14px;
                                line-height:18px;
                            }
                        }
                        p {
                            @media (max-width: $break_767) {
                                font-size:14px;
                                line-height:18px;
                            }
                        }
                    }
                    .bottom-txt {
                        color:$grey2-clr;
                        @media (max-width: $break_767) {
                            font-size:14px;
                            line-height:18px;
                        }
                        a {
                            color:$black-light;
                        }
                    }
                }
            }
        }
        /* ---------------------------- */
        .content2 {
            height:calc(100vh - 150px);
            padding-bottom:60px;
            overflow-y:auto;
            &::-webkit-scrollbar {
                display: none;
            }
            .banner-uc1 {
                background-color:$blue-bg-clr;
                text-align: center;
                padding:60px;
                position: relative;
                border-radius:20px;
                @media (max-width: $break_767) {
                    margin:0 -15px;
                    border-radius:0;
                    padding:65px 15px;
                }
                .top-ptrn {
                    max-width:64px;
                    width:100%;
                    position: absolute;
                    top:40px;
                    left:40px;
                    @media (max-width: $break_767) {
                        max-width:34px;
                        top:15px;
                        left:15px;
                    }
                    img {
                        max-width:100%;
                    }
                }
                h5 {
                    font-size:24px;
                    line-height:30px;
                    font-weight:700;
                    margin-bottom:24px;
                    max-width:482px;
                    display: inline-block;
                    @media (max-width: $break_767) {
                        font-size:22px;
                        line-height:28px;
                        margin-bottom:10px;
                        display: block;
                    }
                }
                p {
                    font-size:20px;
                    line-height:26px;
                    color:$grey2-clr;
                    margin-bottom:40px;
                    max-width:520px;
                    display: inline-block;
                    @media (max-width: $break_767) {
                        font-size:14px;
                        line-height:18px;
                        display: block;
                        margin-bottom:20px;
                    }
                }
                .btn {
                    max-width:305px;
                    width:100%;
                    @media (max-width: $break_767) {
                        max-width:174px;
                    }
                }
                .bottom-ptrn {
                    max-width:64px;
                    width:100%;
                    position: absolute;
                    right:40px;
                    bottom:40px;
                    @media (max-width: $break_767) {
                        max-width:34px;
                        bottom:15px;
                        right:15px;
                    }
                    img {
                        max-width:100%;
                    }
                }
            }
            /* ---------------------------- */
            .followed-appeals {
                margin-top:100px;
                @media (max-width: $break_767) {
                    margin-top:60px;
                }
                &.followed-appeals2 {
                    margin-top:60px;
                    @media (max-width: $break_767) {
                        margin-top:40px;
                    }
                }
            }
            /* ---------------------------- */
            .my-appeals1 {
                padding-bottom:40px;
                border-bottom: 1px solid $grey-clr;
                .bottom-btn-sec {
                    margin-top:20px;
                    text-align: center;
                    @media (min-width: $break_767 + 1px) {
                        display: none;
                    }
                    .btn {
                        width:100%;
                        max-width:175px;
                        font-size:14px;
                        line-height:20px;
                        padding: 10px 20px;
                    }
                }
            }
        }
        /* ---------------------------- */
        .content3 {
            height:calc(100vh - 150px);
            padding-bottom:60px;
            overflow-y:auto;
            &::-webkit-scrollbar {
                display: none;
            }
            .boxi {
                border:1px solid $grey-clr;
                border-radius:20px;
                margin-bottom:20px;
                .tab-header {
                    border-bottom:1px solid $grey-clr;
                    display: flex;
                    justify-content: space-between;
                    padding: 40px;
                    align-items: center;
                    flex-wrap:wrap;
                    // --------------------------------
                    @media (max-width: $break_991) {
                        padding:30px 20px;
                    }
                    h4 {
                        font-size: 24px;
                        line-height: 30px;
                        font-weight: 700;
                        margin: 0;
                        // --------------------------------
                        @media (max-width: $break_991) {
                            font-size:18px;
                            line-height:24px;
                            margin-bottom:10px;
                        }
                        span {
                            display: block;
                            margin-top:10px;
                            font-size:16px;
                            line-height:22px;
                            color:$grey2-clr;
                            font-weight:400;
                        }
                    }
                    .btn-sec {
                        display: flex;
                        .btn {
                            margin-left:5px;
                            font-size: 14px;
                            line-height: 18px;
                            padding: 10px 25px;
                            min-width: 130px;
                            // --------------------------------
                            @media (max-width: $break_991) {
                                margin-left:0;
                                margin-right:5px;
                            }
                            // --------------------------------
                            @media (max-width: $break_767) {
                                min-width:unset;
                            }
                            &.dropdown-toggle {
                                // --------------------------------
                                @media (max-width: $break_767) {
                                    padding-right:50px;
                                }
                                &:after {
                                    content: "\f107";
                                    border: none;
                                    font-family: FontAwesome;
                                    font-size: 16px;
                                    right: 30px;
                                    top: 8px;
                                    position: absolute;
                                    vertical-align: unset;
                                    margin: 0;
                                }
                            }
                        }
                        .dropdown-menu {
                            font-size: 14px;
                            line-height:18px;
                            border-radius: 10px;
                            padding: 5px;
                            width: calc(100% - 5px);
                            background-color: $white-clr;
                            .dropdown-item {
                                text-align: center;
                                background-color:$white-clr;
                                border-radius:5px;
                                padding: 5px;
                                &:hover {
                                    background-color:$blue-bg-clr;
                                }
                            }
                        }
                    }
                }
                .tab-body {
                    padding:0 40px;
                    // --------------------------------
                    @media (max-width: $break_991) {
                        padding:0 20px;
                    }
                    .table {
                        &::-webkit-scrollbar {
                            display: none;
                        }
                        tr {
                            vertical-align:middle;
                        }
                        th {
                            border-bottom:none;
                            border-top:none;
                            vertical-align:middle;
                            min-width: 105px;
                            padding:24px 0;
                            // --------------------------------
                            @media (max-width: $break_991) {
                                min-width:110px;
                            }
                            &:nth-child(2) {
                                min-width: 150px;
                                padding-right: 20px;
                            }
                            &:last-child {
                                min-width:40px;
                                text-align: center;
                            }
                            a {
                                color:$primary-clr;
                                font-size:14px;
                                line-height:18px;
                                font-weight:600;
                                display: inline-block;
                                i {
                                    margin-left:10px;
                                }
                            }
                        }
                        td {
                            border-color:$grey-clr;
                            vertical-align:middle;
                            font-size:14px;
                            line-height:18px;
                            padding:20px 0;
                            &:nth-child(6) {
                                padding-right:20px;
                                min-width:210px;
                                // --------------------------------
                                @media (max-width: $break_991) {
                                    padding-right:10px;
                                }
                            }
                            &:last-child {
                                text-align: center;
                            }
                            small {
                                color:$grey2-clr;
                                font-size:12px;
                                line-height:16px;
                                display: block;
                            }
                            .sh-hd {
                                color:#4CA4FD;
                            }
                            .icons {
                                display: inline-block;
                                width:32px;
                                height:32px;
                                line-height:32px;
                                border:1px solid $grey-clr;
                                border-radius:50%;
                                img {
                                    width:12px;
                                }
                            }
                        }
                    }
                }
                .pagination-mcpg {
                    text-align: center;
                    margin:40px 0;
                    // --------------------------------
                    @media (max-width: $break_991) {
                        margin:30px 0;
                    }
                    ul {
                        li {
                            display: inline-block;
                            margin:0 2;
                            a {
                                display: inline-block;
                                width:28px;
                                height:28px;
                                font-size:14px;
                                line-height:28px;
                                text-align: center;
                                border-radius:5px;
                                border:1px solid $grey-clr;
                                color:$black-light;
                                &:hover, &.active {
                                    color:$white-clr;
                                    border-color:$primary-clr;
                                    background-color:$primary-clr;
                                }
                            }
                        }
                    }
                }
                /* ---------------------------- */
                &.boxi2 {
                    // --------------------------------
                    @media (max-width: $break_767) {
                        border:none;
                        border-bottom: 1px solid $grey-clr;
                        border-radius:0;
                        padding-bottom:40px;
                        margin-bottom:40px;
                    }
                    .tab-header {
                        padding:30px 40px 24px 40px;
                        // --------------------------------
                        @media (max-width: $break_767) {
                            padding:0;
                            border-bottom: none;
                        }
                        h4 {
                            // --------------------------------
                            @media (max-width: $break_767) {
                                margin-bottom:20px;
                            }
                            span {
                                // --------------------------------
                                @media (max-width: $break_767) {
                                    font-size:14px;
                                    line-height:18px;
                                }
                            }
                        }
                    }
                    .tab-body {
                        // --------------------------------
                        @media (max-width: $break_767) {
                            padding:0;
                        }
                        .table {
                            // --------------------------------
                            @media (max-width: $break_767) {
                                display: none;
                            }
                            th {
                                width: 1%;
                                min-width: 150px;
                                &:last-child {
                                    min-width:unset;
                                    text-align: right;
                                }
                            }
                            td {
                                &:last-child {
                                    text-align: right;
                                }
                            }
                        }
                    }
                    .mobile-sec {
                        display: none;
                        // --------------------------------
                        @media (max-width: $break_767) {
                            display: block;
                        }
                        ul {
                            li {
                                border:1px solid $grey-clr;
                                margin-bottom:10px;
                                padding:15px 20px;
                                border-radius:14px;
                                &:last-child {
                                    margin-bottom:0;
                                }
                                h5 {
                                    margin-bottom:5px;
                                    font-size:16px;
                                    line-height:22px;
                                    display: flex;
                                    justify-content:space-between;
                                    span {
                                        &:first-child {
                                            font-weight:600;
                                        }
                                        &:last-child {
                                            font-weight: normal;
                                        }
                                    }
                                }
                                p {
                                    margin-bottom:0;
                                    font-size:14px;
                                    line-height:18px;
                                    color:$grey2-clr;
                                    span {
                                        &:first-child {}
                                        &:last-child {
                                            margin-left:15px;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        /* ---------------------------- */
        .content4 {
            height:calc(100vh - 150px);
            padding-bottom:60px;
            overflow-y:auto;
            &::-webkit-scrollbar {
                display: none;
            }
            .g-heading {
                margin-bottom:40px;
                @media (max-width: $break_767) {
                    margin-bottom:25px;
                }
                h5 {
                    font-size:24px;
                    line-height:30px;
                    font-weight: 700;
                    margin-bottom:10px;
                    @media (max-width: $break_767) {
                        font-size:22px;
                        line-height:28px;
                    }
                }
                p {
                    font-size:16px;
                    line-height:22px;
                    margin-bottom:0;
                    color:$grey2-clr;
                }
            }
            .edit-box {
                border:1px solid $grey-clr;
                border-radius:20px;
                padding:40px;
                position: relative;
                margin-bottom:24px;
                @media (max-width: $break_767) {
                    padding:20px 24px;
                    border-radius:14px;
                    margin-bottom:10px;
                }
                label {
                    display: block;
                    font-size:14px;
                    line-height:18px;
                    color:$grey2-clr;
                    margin-bottom:20px;
                    @media (max-width: $break_767) {
                        margin-bottom:10px;
                    }
                }
                p {
                    font-size:20px;
                    line-height:26px;
                    margin-bottom:0;
                    @media (max-width: $break_767) {
                        font-size:16px;
                        line-height:22px;
                    }
                }
                a {
                    width:40px;
                    height:40px;
                    display: inline-block;
                    position: absolute;
                    top:24px;
                    right:24px;
                    @media (max-width: $break_767) {
                        width:30px;
                        height:30px;
                        top:20px;
                        right:20px;
                    }
                    img {
                        width:100%;
                    }
                }
            }
            .edit-box2 {
                border:1px solid $grey-clr;
                border-radius:20px;
                padding:33px 40px;
                position: relative;
                @media (max-width: $break_767) {
                    padding:20px 24px;
                    border-radius:14px;
                }
                p {
                    font-size:16px;
                    line-height:22px;
                    margin-bottom:0;
                    img {
                        position: relative;
                        top:-2px;
                    }
                }
                .icon-sec {
                    position: absolute;
                    right:24px;
                    top:50%;
                    transform:translateY(-50%);
                    @media (max-width: $break_767) {
                        right:20px;
                    }
                    a {
                        width:40px;
                        height:40px;
                        display: inline-block;
                        @media (max-width: $break_767) {
                            width:30px;
                            height:30px;
                        }
                        img {
                            width:100%;
                        }
                    }
                }
            }
            .avt-edit-box {
                border:1px solid $grey-clr;
                border-radius:20px;
                padding:40px;
                position: relative;
                margin-bottom:24px;
                text-align: center;
                @media (max-width: $break_767) {
                    padding:20px;
                    border-radius:14px;
                    margin-bottom:10px;
                }
                .avt-sec {
                    display: block;
                    margin-bottom:24px;
                    @media (max-width: $break_767) {
                        margin-bottom:15px;
                    }
                    img {
                        max-width:180px;
                        @media (max-width: $break_767) {
                            max-width:120px;
                        }
                    }
                }
                p {
                    max-width:255px;
                    font-size:14px;
                    line-height:18px;
                    color:$grey2-clr;
                    display: inline-block;
                    margin-bottom:0;
                }
                a {
                    width:40px;
                    height:40px;
                    display: inline-block;
                    position: absolute;
                    top:24px;
                    right:24px;
                    @media (max-width: $break_767) {
                        top:20px;
                        right:20px;
                        width:30px;
                        height:30px;
                    }
                    img {
                        width:100%;
                    }
                }
            }
            hr {
                margin:40px 0;
                border-color:rgba($grey-clr, 0.6);
            }
            .pay_btn_sec {
                margin-top:20px;
                .btn {
                    padding:14px 40px;
                    // --------------------------------
                    @media (max-width: $break_767) {
                        padding:14px 24px;
                    }
                }
            }
            .txt_sec {
                p {
                    font-size:15px;
                    line-height:21px;
                    color:$grey2-clr;
                    margin-bottom:15px;
                    &:last-child {
                        margin-bottom:40px;
                        @media (max-width: $break_767) {
                            margin-bottom:20px;
                        }
                    }
                }
            }
            .newsletter {
                display: flex;
                flex-wrap:wrap;
                justify-content: space-between;
                li {
                    width:calc(50% - 10px);
                    @media (max-width: $break_767) {
                        width:100%;
                    }
                    .boxi {
                        border:1px solid $grey-clr;
                        border-radius:20px;
                        padding:20px;
                        margin-bottom:20px;
                        @media (max-width: $break_767) {
                            border-radius:14px;
                            margin-bottom:10px;
                        }
                        h5 {
                            font-size:20px;
                            line-height:26px;
                            font-weight: 600;
                            margin-bottom:10px;
                            @media (max-width: $break_767) {
                                font-size:16px;
                                line-height:22px;
                            }
                        }
                        p {
                            font-size:14px;
                            line-height:18px;
                            margin-bottom:10px;
                        }
                        .left_span {
                            font-size:14px;
                            line-height:18px;
                            color:$grey2-clr;
                        }
                        .img_sec {
                            border-radius:12px;
                            overflow: hidden;
                            position: relative;
                            .main_img {
                                width:100%;
                                height:100%;
                            }
                            .selects {
                                position: absolute;
                                top:10px;
                                right:10px;
                                &.added {
                                    box-shadow:$shadow-1;
                                }
                                img {
                                    max-width:28px;
                                    width: 100%;
                                    @media (max-width: $break_767) {
                                        max-width:24px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .text-bot {
                font-size:14px;
                line-height:18px;
                margin-top:20px;
                @media (max-width: $break_767) {
                    margin-top:15px;
                }
                a {
                    color:$black-light;
                    text-decoration: underline;
                    &:hover {
                        color:$primary-clr;
                    }
                }
            }
            .text-bot2 {
                margin-top:20px;
                @media (max-width: $break_767) {
                    margin-top:15px;
                }
                .btn {
                    padding:14px 24px;
                    width:100%;
                    max-width:220px;
                }
            }
        }
    }
}

// User Edit Modal
.user-modal {
    .modal-dialog {
        max-width:630px;
        .modal-content {
            border-radius:20px;
            border:none;
            // --------------------------------
            @media (max-width: $break_767) {
                border-radius:14px;
            }
            .modal-body {
                padding:100px 60px;
                // --------------------------------
                @media (max-width: $break_767) {
                    padding:30px 20px;
                }
                h5 {
                    font-size:36px;
                    line-height:44px;
                    font-weight:700;
                    margin-bottom:40px;
                    text-align: center;
                    letter-spacing:-1px;
                    // --------------------------------
                    @media (max-width: $break_767) {
                        font-size:22px;
                        line-height:28px;
                        margin-bottom:25px;
                    }
                }
                .close {
                    position: absolute;
                    opacity: 1;
                    top: 45px;
                    right: 45px;
                    width:14px;
                    height:14px;
                    // --------------------------------
                    @media (max-width: $break_767) {
                        top:35px;
                        right:20px;
                    }
                    &:focus {
                        outline:none;
                    }
                    img {
                        width:100%;
                    }
                }
                form {
                    max-width:414px;
                    margin:auto;
                }
                .form-group {
                    label {}
                    .form-control {}
                    .card_img {
                        position: relative;
                        img {
                            position: absolute;
                            max-width:100%;
                            top:50%;
                            right:15px;
                            transform:translateY(-50%);
                        }
                    }
                }
                .btn {
                    // --------------------------------
                    @media (max-width: $break_767) {
                        font-size:16px;
                        line-height:20px;
                    }
                    &.btn-op25 {
                        opacity:0.25;
                    }
                }
                .input-phone {
                    .btn {
                        position: relative;
                        z-index: 9;
                        background: transparent;
                        margin-top: 2px;
                        margin-left: 20px;
                        font-weight: normal;
                        font-size: 20px;
                        border: none;
                        padding: 0;
                        height: 50px;
                        line-height: 25px;
                        // --------------------------------
                        @media (max-width: $break_767) {
                            font-size:14px;
                            line-height:18px;
                        }
                        &:after {
                            display: none;
                        }
                        .caret {
                            position: relative;
                            border: none;
                            margin: 0;
                            width: auto;
                            height: auto;
                            &:after {
                                content: "\f107";
                                border: none;
                                font-family: FontAwesome;
                                font-size: 16px;
                                right: -10px;
                                top: -14px;
                                position: absolute;
                                vertical-align: unset;
                                margin: 0;
                                // --------------------------------
                                @media (max-width: $break_767) {
                                    top:-10px;
                                }
                            }
                        }
                    }
                    .phoneNumber {
                        font-size: 1em;
                        line-height: 22px;
                        padding: 5px 24px;
                        background-color: #ffffff;
                        color: #101010;
                        border: 1px solid #DAE0E6;
                        border-radius: 12px;
                        padding-left: 130px;
                        // --------------------------------
                        @media (max-width: $break_767) {
                            font-size:14px;
                            line-height:18px;
                            padding-left: 100px;
                        }
                    }
                    .dropdown-menu {
                        min-width: 6.5rem;
                        text-align: center;
                        font-size: 20px;
                        li {
                            display: inline;
                            font-size: 14px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 18px;
                            letter-spacing: 0em;
                            text-align: left;
                        }
                    }
                }
            }
        }
    }
    /* User Modal 2 */
    &.user-modal2 {
        .modal-dialog {
            .modal-content {
                .modal-body {
                    padding:40px;
                    // --------------------------------
                    @media (max-width: $break_767) {
                        padding:20px;
                    }
                    .close {
                        display: none;
                        // --------------------------------
                        @media (max-width: $break_767) {
                            display: inline-block;
                            top:25px;
                        }
                    }
                    .top-ptrn {
                        margin-bottom:55px;
                        // --------------------------------
                        @media (max-width: $break_767) {
                            margin-bottom:50px;
                        }
                        img {
                            max-width:100%;
                            // --------------------------------
                            @media (max-width: $break_767) {
                                max-width:45px;
                            }
                        }
                    }
                    h5 {
                        margin-bottom:40px;
                    }
                    .btn {
                        max-width:200px;
                        width:100%;
                        // --------------------------------
                        @media (max-width: $break_767) {
                            max-width:120px;
                            font-size:14px;
                            line-height:18px;
                        }
                    }
                    .bottom-ptrn {
                        text-align: right;
                        margin-top:55px;
                        // --------------------------------
                        @media (max-width: $break_767) {
                            margin-top:10px;
                        }
                        img {
                            max-width:100%;
                            // --------------------------------
                            @media (max-width: $break_767) {
                                max-width:45px;
                            }
                        }
                    }
                }
            }
        }
    }
}